import React from "react";

const GateInfo: React.FC = () => {
  const gateData = {
    refreshTime: "12:34 PM",
    status: "Active",
    condition: "Good",
    openCloseState: "Closed",
    temperature: "35°C",
    voltage: "220V",
  };

  const cardData = [
    {
      label: "Refresh Time",
      value: gateData.refreshTime,
      icon: "⏱️",
    },
    {
      label: "Status",
      value: gateData.status,
      icon: "✅",
    },
    {
      label: "Condition",
      value: gateData.condition,
      icon: "⚙️",
    },
    {
      label: "Open/Close",
      value: gateData.openCloseState,
      icon: "🚪",
    },
    {
      label: "Temperature",
      value: gateData.temperature,
      icon: "🌡️",
    },
    {
      label: "Voltage",
      value: gateData.voltage,
      icon: "🔋",
    },
  ];

  return (
    <div className="grid grid-cols-3 gap-6 w-full mt-6">
      {cardData.map((card, index) => (
        <div
          key={index}
          className="flex flex-col items-center justify-center bg-white shadow-lg p-6 rounded-xl text-center hover:shadow-xl transition-shadow"
        >
          <div className="text-3xl mb-3">{card.icon}</div>
          <h3 className="text-gray-500 font-medium text-sm">{card.label}</h3>
          <p className="text-gray-800 font-bold text-lg">{card.value}</p>
        </div>
      ))}
    </div>
  );
};

export default GateInfo;
